.modal-sorting {
    .modal__header {
        font-weight: 600;
        font-size: 16px;
        color: $color-text-base;
    }
    .modal__wrap {
        min-width: 264px;
        border-radius: 10px;
    }
    .modal__content {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    .checkbox {
        margin-bottom: 10px;
        &__label {
            font-size: 14px;
            color: $color-text-base;
        }
    }
}

.sidebar-stock-m {
    padding: 12px;

    .stock-aside__heading {
        font-size: 14px;
    }

    .stock-aside__catalogs {
        margin-bottom: 20px;
        border-bottom: unset;
    }
}

.stock-m {
    &__back {
        padding: 20px 20px 15px;
    }

    &__no-active {
        position: sticky;
        top: 0;
        left: 0;
        z-index: 5;
        line-height: 24px;
    }

    &__filters {
        padding: 25px 50px 25px 30px;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;

        @media screen and (min-width: 768px) {
            padding: 25px 45px 25px 45px;
            max-width: 350px;
            margin: 0 auto;
        }

        .sorting {
            position: relative;

            &__selected-position {
                border-bottom: unset;
            }

            &__btn {
                display: block;
                color: $color-text-base;
                font-weight: 600;

                &:after {
                    display: none;
                }
            }

            &__name {
                display: inline;
                position: relative;

                &:after {
                    content: "";
                    position: absolute;
                    display: flex;
                    width: 8px;
                    height: 8px;
                    top: 6px;
                    right: -12px;
                    background: url("~images/active_custom_card/arrow-point-to-down.png");
                    background-repeat: no-repeat;
                    background-position: 50%;
                    background-size: cover;
                    opacity: 0.6;
                    cursor: pointer;
                }
            }
        }
    }
    .stock-items-filter__show-all {
        .checkbox__label {
            margin: 0 0 0 10px;
        }

    }
}

.stock-m-items {
    // общие для всех режимов отображения изменения
    .stock-card-vertical {
        &__bar {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

    }
    .stock-card-vertical-wrapper:hover .stock-card-vertical {
        box-shadow: unset;
    }

    //
    //
    //

    // режим отображения по 1 товару, мобильное
    .stock-card-container.horizontal {
        .stock-card-vertical-wrapper {
            display: initial;
            @media screen and (min-width: 768px) {
                display: none;
            }
        }

        .stock-card-horizontal {
            display: none;
            margin-bottom: 25px;
            @media screen and (min-width: 768px) {
                display: flex;
            }
        }

        background: rgba(49, 140, 202, 0.12);

        .org-get-position-btn {
            margin-right: 15px;
            font-size: 24px;
        }

        .org-edit-position-btn {
            font-size: 23px;
        }

        .checkbox {
            min-width: 23px;
            min-height: 23px;

            &__label {
                &:before {
                    width: 23px;
                    height: 22px;
                }

                &:after {
                    left: 1px;
                    top: 6px;
                    width: 13px;
                    height: 7px;
                }
            }
        }

        .stock-card-vertical-wrapper {
            margin-bottom: 15px;
        }
        .stock-card-vertical {
            &__heading {
                font-size: 16px;
                margin-bottom: 20px;
                height: auto;
            }

            &__price {
                font-size: 24px;
                line-height: unset;

                span {
                    font-size: 16px;
                }
            }

            &__org {
                font-size: 12px;
            }

            &__wrapper {
                height: unset;
            }

            &__image {
                max-height: 300px;
            }

            &__main {
                margin-bottom: 20px;
            }

            &__buy {
                max-width: 142px;
            }

            .collapse-section {
                margin-bottom: 20px;

                &__heading {
                    font-size: 14px;
                }

                &__body {
                    line-height: 20px;
                }
            }

            .stock-rows-legend {
                margin-top: 10px;
                flex-wrap: wrap;
                justify-content: center;
                margin-right: -15px;

                &__item {
                    margin-bottom: 10px;
                }
            }

            .stock-rows-help {
                margin: 10px auto 15px;
            }
        }
    }

    // отображение по несколько, мобильное
    .stock-card-container.vertical {
        padding: 0 4px;

        .checkbox {
            min-height: 18px;
        }

        .org-get-position-btn {
            margin-right: 20px;
        }
        
        // отображение по умолчанию
        .stock-card-vertical-wrapper {
            width: 49.99999%;

            @media screen and (min-width: 481px) {
                width: 33.33333%;
            }

            @media screen and (min-width: 681px) {
                width: 24.99999%;
            }

            @media screen and (min-width: 921px) {
                width: 19.999999%;
            }
        }

        // изменения в самой карточке
        .stock-card-vertical {
            padding: 9px 7px 18px;

            &__heading {
                font-size: 12px;
                line-height: 20px;
                margin-bottom: 15px;
            }

            &__price-wrapper {
                margin-bottom: 10px;
            }

            &__price {
                font-size: 16px;
                line-height: unset;

                span {
                    font-size: 12px;
                }
            }

            &__org {
                font-size: 11px;
            }

            &__wrapper {
                height: 147px;
            }

            &__image {
                max-height: 147px;
            }

            &__buy {
                max-width: unset;
            }
        }
    }
}

// отбивка товаров
.stock-m-item-w {
    margin-bottom: 15px;
}
